
body {
  /* radial-gradient 设置径向渐变色背景 */
  background: radial-gradient(
    200% 100% at bottom center,
    #f7f7b6,
    #e96f92,
    #1b2947
  );
  background: radial-gradient(
    220% 105% at top center,
    #1b2947 10%,
    #75517d 40%,
    #e96f92 65%,
    #f7f7b6
  );
  background-attachment: fixed; /* 设置背景图片不随页面滚动（相当于body来定位） */
  overflow: hidden;
}

@-webkit-keyframes rotate-data-v-e2b83f8c {
0% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
}
100% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
}
}
@keyframes rotate-data-v-e2b83f8c {
0% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
}
100% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
}
}
.stars[data-v-e2b83f8c] {
  -webkit-transform: perspective(500px);
          transform: perspective(500px);
  /* 设置镜头到物体的距离 */
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: absolute;
  -webkit-perspective-origin: 50% 100%;
          perspective-origin: 50% 100%;
  left: 50%;
  bottom: 0;
  -webkit-animation: rotate-data-v-e2b83f8c 90s infinite linear;
          animation: rotate-data-v-e2b83f8c 90s infinite linear;
}
.star[data-v-e2b83f8c] {
  width: 2px;
  height: 2px;
  background: #f7f7b8;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
